.stats {
    display: flex;
    border-top: 1px solid #a9a9a9;
  }
  
  .stats > li {
    display: flex;
    justify-content: center;
    flex-direction: column;
    width: 33.3333%;
    height: 80px;
    text-align: center;
    background-color: #f0ecec;
  }
  
  .stats > li:not(:last-child) {
    border-right: 1px solid #a9a9a9;
  }
  
  .label {
    margin-bottom: 5px;
  }
  
  .quantity {
    font-weight: 700;
    font-size: 16px;
  }
  
  .tag,
  .location,
  .label {
    color: #a9a9a9;
  }
  
  .statistics {
    width: 300px;
    margin: 0 auto 30px;
    padding-top: 30px;
    overflow: hidden;
    border-radius: 5px;
    background-color: #fff;
  }
  
  .title {
    text-align: center;
    margin-bottom: 30px;
  }
  
  .stat-list {
    display: flex;
  }
  
  .item {
    width: 25%;
    height: 60px;
    display: flex;
    flex-direction: column;
    text-align: center;
    justify-content: center;
    color: #fff;
  }
  
  .item > .label {
    color: #fff;
  }